// ÈªòËÆ§ÈÖçÁΩÆ
const defaultOSConfig: OSConfig = {
  name: "Unknown",
  image: "/assets/logo/linux.svg",
  keywords: ["unknown"],
};

/**
 * Ê†πÊçÆËæìÂÖ•Â≠óÁ¨¶‰∏≤Êü•ÊâæÂåπÈÖçÁöÑÊìç‰ΩúÁ≥ªÁªüÈÖçÁΩÆ
 * @param osString - Êìç‰ΩúÁ≥ªÁªüÁõ∏ÂÖ≥ÁöÑÂ≠óÁ¨¶‰∏≤
 * @returns ÂåπÈÖçÁöÑÊìç‰ΩúÁ≥ªÁªüÈÖçÁΩÆÔºåÂ¶ÇÊûúÊ≤°ÊúâÂåπÈÖçÂàôËøîÂõûÈªòËÆ§ÈÖçÁΩÆ
 */
function findOSConfig(osString: string): OSConfig {
  if (!osString) {
    return defaultOSConfig;
  }

  const normalizedInput = osString.toLowerCase().trim();

  // ÈÅçÂéÜÂåπÈÖçÈÖçÁΩÆ
  for (const config of osConfigs) {
    for (const keyword of config.keywords) {
      if (normalizedInput.includes(keyword)) {
        return config;
      }
    }
  }

  // Â¶ÇÊûúÊ≤°ÊúâÂåπÈÖçÂà∞ÔºåËøîÂõûÈªòËÆ§ÈÖçÁΩÆ
  return defaultOSConfig;
}

/**
 * Ê†πÊçÆËæìÂÖ•Â≠óÁ¨¶‰∏≤ÂåπÈÖçËøîÂõûÊìç‰ΩúÁ≥ªÁªüÂõæÂÉèË∑ØÂæÑ
 * @param osString - Êìç‰ΩúÁ≥ªÁªüÁõ∏ÂÖ≥ÁöÑÂ≠óÁ¨¶‰∏≤
 * @returns ÂåπÈÖçÁöÑÊìç‰ΩúÁ≥ªÁªüÂõæÂÉèË∑ØÂæÑÔºåÂ¶ÇÊûúÊ≤°ÊúâÂåπÈÖçÂàôËøîÂõûÈªòËÆ§ÂõæÂÉè
 */
export function getOSImage(osString: string): string {
  return findOSConfig(osString).image;
}

/**
 * Ëé∑ÂèñÊâÄÊúâÂèØÁî®ÁöÑÊìç‰ΩúÁ≥ªÁªüÂõæÂÉè
 * @returns ÊâÄÊúâÊìç‰ΩúÁ≥ªÁªüÂõæÂÉèÁöÑÊò†Â∞ÑË°®
 */
export function getAllOSImages(): Record<string, string> {
  const imageMap: Record<string, string> = {};

  osConfigs.forEach((config) => {
    const key = config.keywords[0]; // ‰ΩøÁî®Á¨¨‰∏Ä‰∏™ÂÖ≥ÈîÆËØç‰Ωú‰∏∫ÈîÆ
    imageMap[key] = config.image;
  });

  imageMap.unknown = defaultOSConfig.image;

  return imageMap;
}

/**
 * Ê†πÊçÆËæìÂÖ•Â≠óÁ¨¶‰∏≤ÂåπÈÖçËøîÂõûÊìç‰ΩúÁ≥ªÁªüÂêçÁß∞
 * @param osString - Êìç‰ΩúÁ≥ªÁªüÁõ∏ÂÖ≥ÁöÑÂ≠óÁ¨¶‰∏≤
 * @returns ÂåπÈÖçÁöÑÊìç‰ΩúÁ≥ªÁªüÂêçÁß∞
 */
export function getOSName(osString: string): string {
  const config = findOSConfig(osString);

  // Â¶ÇÊûúÂåπÈÖçÂà∞ÂÖ∑‰ΩìÁöÑÊìç‰ΩúÁ≥ªÁªüÔºåËøîÂõûÂÖ∂ÂêçÁß∞
  if (config !== defaultOSConfig) {
    return config.name;
  }

  // Â¶ÇÊûúÊ≤°ÊúâÂåπÈÖçÂà∞Ôºå‰ªéËæìÂÖ•Â≠óÁ¨¶‰∏≤‰∏≠ÊèêÂèñÂêçÁß∞
  if (!osString) {
    return "Unknown";
  }

  // ‰ΩøÁî®Á©∫Ê†ºÊàñÊñúÊù†ÂàÜÂâ≤ÔºåÂèñÁ¨¨‰∏Ä‰∏™ÈÉ®ÂàÜ
  const parts = osString.trim().split(/[\s/]/);
  return parts[0] || "Unknown";
}

/**
 * Ê£ÄÊü•ÊòØÂê¶‰∏∫ÊîØÊåÅÁöÑÊìç‰ΩúÁ≥ªÁªü
 * @param osString - Êìç‰ΩúÁ≥ªÁªüÁõ∏ÂÖ≥ÁöÑÂ≠óÁ¨¶‰∏≤
 * @returns ÊòØÂê¶‰∏∫ÊîØÊåÅÁöÑÊìç‰ΩúÁ≥ªÁªü
 */
export function isSupportedOS(osString: string): boolean {
  if (!osString) return false;

  const config = findOSConfig(osString);
  return config !== defaultOSConfig;
}
