/**
 * Â∞ÜË°®Á§∫Êï∞ÊçÆÂ§ßÂ∞èÁöÑÂ≠óÁ¨¶‰∏≤ÔºàÂ¶Ç '1.5MB', '128*1024gb'ÔºâËΩ¨Êç¢‰∏∫Â≠óËäÇÊï∞„ÄÇ
 * @param str - ËæìÂÖ•ÁöÑÂ≠óÁ¨¶‰∏≤„ÄÇ
 * @returns - ËÆ°ÁÆóÂá∫ÁöÑÂ≠óËäÇÊï∞ÔºànumberÔºâ„ÄÇÂ¶ÇÊûúÊó†Ê≥ïËß£ÊûêÔºåÂàôËøîÂõû 0„ÄÇ
 * @example
 * stringToBytes('1MB');        // 1048576
 * stringToBytes('1 MB');        // 1048576
 * stringToBytes('5.4MB');      // 5662310.4
 * stringToBytes('6,222,765 MB'); // 6525139624935
 * stringToBytes('128*1024gb'); // 140737488355328
 * stringToBytes('1e3kb');       // 1024000 (1000 * 1024)
 * stringToBytes('0.2gb');       // 214748364.8
 * stringToBytes('1024');        // 1024 (ÈªòËÆ§‰∏∫Â≠óËäÇ)
 * stringToBytes('1tb');         // 1099511627776
 */
export function stringToBytes(str: string): number {
  if (typeof str !== "string" || str.length === 0) {
    return 0;
  }
  // ÂÆö‰πâÂçï‰ΩçÂíåÂÆÉ‰ª¨ÁöÑÂ≠óËäÇÂÄçÊï∞ (‰ΩøÁî® 1024 ‰∏∫Âü∫Êï∞)
  const units: { [key: string]: number } = {
    b: 1,
    byte: 1,
    bytes: 1,
    k: 1024,
    kb: 1024,
    kib: 1024,
    kilobyte: 1024,
    m: 1024 ** 2,
    mb: 1024 ** 2,
    mib: 1024 ** 2,
    megabyte: 1024 ** 2,
    g: 1024 ** 3,
    gb: 1024 ** 3,
    gib: 1024 ** 3,
    gigabyte: 1024 ** 3,
    t: 1024 ** 4,
    tb: 1024 ** 4,
    tib: 1024 ** 4,
    terabyte: 1024 ** 4,
    p: 1024 ** 5,
    pb: 1024 ** 5,
    pib: 1024 ** 5,
    petabyte: 1024 ** 5,
  };

  // 1. È¢ÑÂ§ÑÁêÜÂ≠óÁ¨¶‰∏≤ÔºöËΩ¨Â∞èÂÜôÔºåÁßªÈô§ÈÄóÂè∑ÂíåÁ©∫Ê†º
  const cleanStr = str.toLowerCase().replace(/,/g, "").replace(/\s/g, "");

  // 2. ÂàÜÁ¶ªÂçï‰ΩçÂíåÊï∞ÂÄº
  // ÊåâÈïøÂ∫¶ÈôçÂ∫èÊéíÂ∫èÂçï‰ΩçÔºå‰ª•‰ºòÂÖàÂåπÈÖçÈïøÂçï‰ΩçÔºàÂ¶Ç 'kb' ËÄå‰∏çÊòØ 'b'Ôºâ
  const unitKeys = Object.keys(units).sort((a, b) => b.length - a.length);
  const unitRegex = new RegExp(`(${unitKeys.join("|")})$`);

  let unit = "b"; // ÈªòËÆ§‰∏∫ byte
  let numericPart = cleanStr;

  const match = cleanStr.match(unitRegex);
  if (match) {
    unit = match[1];
    // ‰ªéÂ≠óÁ¨¶‰∏≤‰∏≠ÁßªÈô§Âçï‰ΩçÔºåÂæóÂà∞Á∫ØÊï∞ÂÄºÈÉ®ÂàÜ
    numericPart = cleanStr.substring(0, cleanStr.length - unit.length);
  }

  // Â¶ÇÊûúÊï∞ÂÄºÈÉ®ÂàÜ‰∏∫Á©∫Ôºà‰æãÂ¶ÇËæìÂÖ• "kb"ÔºâÔºåÂàôËÆ§‰∏∫Êï∞ÂÄºÊòØ 1
  if (numericPart === "") {
    numericPart = "1";
  }

  try {
    // 3. ËÆ°ÁÆóÊï∞ÂÄºÈÉ®ÂàÜ
    // ‰ΩøÁî® Function ÊûÑÈÄ†ÂáΩÊï∞Êù•ÂÆâÂÖ®Âú∞ËØÑ‰º∞ÂèØËÉΩÂåÖÂê´‰πòÊ≥ïÊàñÁßëÂ≠¶ËÆ∞Êï∞Ê≥ïÁöÑË°®ËææÂºè
    // Ê≥®ÊÑèÔºöËøô‰ªçÁÑ∂ÂÅáËÆæËæìÂÖ•Ê∫êÊòØÂèØ‰ø°ÁöÑÔºåÂõ†‰∏∫ÂÆÉËÉΩÊâßË°åÁÆÄÂçïÁöÑÊï∞Â≠¶ËøêÁÆó
    const value = new Function(`return ${numericPart}`)();

    if (isNaN(value)) {
      return 0;
    }

    // 4. ‰πò‰ª•Âçï‰ΩçÂØπÂ∫îÁöÑÂÄçÊï∞
    const multiplier = units[unit];
    return Math.round(value * multiplier);
  } catch (error) {
    // Â¶ÇÊûúË°®ËææÂºèÊó†ÊïàÔºà‰æãÂ¶Ç "abc-gb"ÔºâÔºåÂàôÊçïËé∑ÈîôËØØÂπ∂ËøîÂõû 0
    console.error(`Error parsing string "${str}":`, error);
    return 0;
  }
}

export function formatBytes(bytes: number): string {
  const units = ["B", "KB", "MB", "GB", "TB", "PB"];
  let size = bytes;
  let unitIndex = 0;

  while (size >= 1024 && unitIndex < units.length - 1) {
    size /= 1024;
    unitIndex++;
  }

  if (unitIndex === 0) {
    // Âçï‰Ωç‰∏∫BÔºå‰∏çÊòæÁ§∫Â∞èÊï∞
    return `${Math.round(size)} ${units[unitIndex]}`;
  } else if (unitIndex >= 2 && bytes >= 1024**3) {
    return `${size.toFixed(2)} ${units[unitIndex]}`;
  } else if (size > 99.99) {
    return `${size.toFixed(1)} ${units[unitIndex]}`;
  } else {
    // Â∞è‰∫éÁ≠â‰∫é‰∏§‰ΩçÊï∞ÔºåÊòæÁ§∫2‰ΩçÂ∞èÊï∞
    return `${size.toFixed(2)} ${units[unitIndex]}`;
  }
}
