"use client";

import React from "react";
import { useRPC2Call } from "./RPC2Context";

export type NodeBasicInfo = {
  /** ËäÇÁÇπÂîØ‰∏ÄÊ†áËØÜÁ¨¶ */
  uuid: string;
  /** ËäÇÁÇπÂêçÁß∞ */
  name: string;
  /** CPUÂûãÂè∑ */
  cpu_name: string;
  /** ËôöÊãüÂåñ */
  virtualization: string;
  /** Á≥ªÁªüÊû∂ÊûÑ */
  arch: string;
  /** CPUÊ†∏ÂøÉÊï∞ */
  cpu_cores: number;
  /** Êìç‰ΩúÁ≥ªÁªü */
  os: string;
  /** ÂÜÖÊ†∏ÁâàÊú¨ */
  kernel_version: string;
  /** GPUÂûãÂè∑ */
  gpu_name: string;
  /** Âú∞Âå∫Ê†áËØÜ */
  region: string;
  /** ÊÄªÂÜÖÂ≠ò(Â≠óËäÇ) */
  mem_total: number;
  /** ÊÄª‰∫§Êç¢Á©∫Èó¥(Â≠óËäÇ) */
  swap_total: number;
  /** ÊÄªÁ£ÅÁõòÁ©∫Èó¥(Â≠óËäÇ) */
  disk_total: number;
  /** ÁâàÊú¨Âè∑ */
  version: string;
  /** ÊùÉÈáç */
  weight: number;
  /** ‰ª∑Ê†º */
  price: number;
  tags: string;
  /** Ë¥¶ÂçïÂë®ÊúüÔºàÂ§©Ôºâ*/
  billing_cycle: number;
  /** Ë¥ßÂ∏Å */
  currency: string;
  /** ÂàÜÁªÑ */
  group: string;
  /** ÊµÅÈáèÈòàÂÄº */
  traffic_limit: number;
  /** ÊµÅÈáèÈòàÂÄºÁ±ªÂûã */
  traffic_limit_type: undefined | "sum" | "max" | "min" | "up" | "down";
  /** ËøáÊúüÊó∂Èó¥ */
  expired_at: string;
  /** ÂàõÂª∫Êó∂Èó¥ */
  created_at: string;
  /** Êõ¥Êñ∞Êó∂Èó¥ */
  updated_at: string;
  ipv4?: string; 
  ipv6?: string;
};

interface NodeListContextType {
  nodeList: NodeBasicInfo[] | null;
  isLoading: boolean;
  error: string | null;
  refresh: () => void;
}

const NodeListContext = React.createContext<NodeListContextType | undefined>(
  undefined
);

export const NodeListProvider: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  const [nodeList, setNodeList] = React.useState<NodeBasicInfo[] | null>(null);
  const [isLoading, setIsLoading] = React.useState<boolean>(true);
  const [error, setError] = React.useState<string | null>(null);
  const { call } = useRPC2Call();

  const refresh = () => {
    // setIsLoading(true);
    setError(null);
    // ÈÄöËøá RPC2 Ëé∑ÂèñËäÇÁÇπÂü∫Êú¨‰ø°ÊÅØ
    call<{ uuid?: string }, Record<string, any>>("common:getNodes")
      .then((result) => {
        if (!result || typeof result !== "object") {
          setNodeList([]);
          return;
        }
        // Â∞Ü { [uuid]: Client } ËΩ¨Êç¢‰∏∫ NodeBasicInfo[]
        const list: NodeBasicInfo[] = Object.values(result).map((n: any) => ({
          uuid: n.uuid,
          name: n.name,
          cpu_name: n.cpu_name,
          virtualization: n.virtualization,
          arch: n.arch,
          cpu_cores: n.cpu_cores,
          os: n.os,
          kernel_version: n.kernel_version,
          gpu_name: n.gpu_name,
          region: n.region,
          mem_total: n.mem_total,
          swap_total: n.swap_total,
          disk_total: n.disk_total,
          // ÂÖºÂÆπÊóßÂ≠óÊÆµÔºåËã•Êó†ÁâàÊú¨‰ø°ÊÅØÂàôÁªôÁ©∫‰∏≤
          version: n.version ?? "",
          weight: n.weight ?? 0,
          price: n.price ?? 0,
          tags: n.tags ?? "",
          billing_cycle: n.billing_cycle ?? 0,
          currency: n.currency ?? "",
          group: n.group ?? "",
          traffic_limit: n.traffic_limit ?? 0,
          traffic_limit_type: n.traffic_limit_type,
          expired_at: n.expired_at ?? "",
          created_at: n.created_at ?? "",
          updated_at: n.updated_at ?? "",
          ipv4: n.ipv4,
          ipv6: n.ipv6,
        }));
        setNodeList(list);
      })
      .catch((err: any) => {
        setError(err?.message || "An error occurred while fetching data");
        setNodeList([]);
      })
      .finally(() => {
        setIsLoading(false);
      });
  };

  React.useEffect(() => {
    refresh();
  }, []);
  return (
    <NodeListContext.Provider value={{ nodeList, isLoading, error, refresh }}>
      {children}
    </NodeListContext.Provider>
  );
};

export const useNodeList = () => {
  const context = React.useContext(NodeListContext);
  if (!context) {
    throw new Error("useNodeList must be used within a NodeListProvider");
  }
  return context;
};
