import * as React from "react";
import { Box } from "@radix-ui/themes";

interface FlagProps {
  flag: string; // Âú∞Âå∫‰ª£Á†Å (‰æãÂ¶Ç "SG", "US") ÊàñÊóóÂ∏ú emoji (‰æãÂ¶Ç "üá∏üá¨", "üá∫üá≥")
  size?: string; // ÂèØÈÄâÁöÑÂ∞∫ÂØ∏ propÔºåÁî®‰∫éÊú™Êù•Êâ©Â±ï
}

/**
 * ÁÆóÊ≥ïÔºöÂ∞ÜÁî±‰∏§‰∏™Âå∫ÂüüÊåáÁ§∫Á¨¶Á¨¶Âè∑ÁªÑÊàêÁöÑ emoji ËΩ¨Êç¢‰∏∫ÂØπÂ∫îÁöÑ‰∏§Â≠óÊØçÂõΩÂÆ∂‰ª£Á†Å„ÄÇ
 * ‰æãÂ¶ÇÔºöüá∏üá¨ (Áî±‰∏§‰∏™Âå∫ÂüüÊåáÁ§∫Á¨¶ÁªÑÊàê) -> SG
 * @param emoji ËæìÂÖ•ÁöÑ emoji Â≠óÁ¨¶‰∏≤
 * @returns ËΩ¨Êç¢ÂêéÁöÑ‰∏§Â≠óÊØçÂõΩÂÆ∂‰ª£Á†ÅÔºà‰æãÂ¶Ç "SG"ÔºâÔºåÂ¶ÇÊûú‰∏çÊòØÊúâÊïàÁöÑÊóóÂ∏ú emoji ÂàôËøîÂõû null„ÄÇ
 */
const getCountryCodeFromFlagEmoji = (emoji: string): string | null => {
  // ‰ΩøÁî® Array.from() Êù•Ê≠£Á°ÆÂ§ÑÁêÜ Unicode ‰ª£ÁêÜÂØπÔºåÂ∞Ü emoji Â≠óÁ¨¶‰∏≤ÊãÜÂàÜ‰∏∫ÈÄªËæë‰∏äÁöÑÂ≠óÁ¨¶Êï∞ÁªÑ„ÄÇ
  // ÂØπ‰∫é‰∏Ä‰∏™ÂõΩÂÆ∂ÊóóÂ∏ú emojiÔºåchars Êï∞ÁªÑÁöÑÈïøÂ∫¶Â∞ÜÊòØ 2 (ÊØè‰∏™ÂÖÉÁ¥†ÊòØ‰∏Ä‰∏™Âå∫ÂüüÊåáÁ§∫Á¨¶Â≠óÁ¨¶)„ÄÇ
  const chars = Array.from(emoji);

  // ÂõΩÂÆ∂ÊóóÂ∏ú emoji Â∫îËØ•Áî±‰∏î‰ªÖÁî±‰∏§‰∏™Âå∫ÂüüÊåáÁ§∫Á¨¶Â≠óÁ¨¶ÁªÑÊàê
  if (chars.length !== 2) {
    return null;
  }

  // Ëé∑Âèñ‰∏§‰∏™Âå∫ÂüüÊåáÁ§∫Á¨¶Â≠óÁ¨¶ÁöÑ Unicode Á†ÅÁÇπ
  const codePoint1 = chars[0].codePointAt(0)!;
  const codePoint2 = chars[1].codePointAt(0)!;

  // Âå∫ÂüüÊåáÁ§∫Á¨¶Á¨¶Âè∑ÁöÑ Unicode ËåÉÂõ¥ÊòØ‰ªé U+1F1E6 (üá¶) Âà∞ U+1F1FF (üáø)
  const REGIONAL_INDICATOR_START = 0x1F1E6; // üá¶ ÁöÑ Unicode Á†ÅÁÇπ
  const ASCII_ALPHA_START = 0x41; // A ÁöÑ ASCII Á†ÅÁÇπ

  // Ê£ÄÊü•‰∏§‰∏™Á†ÅÁÇπÊòØÂê¶ÈÉΩÂú®Âå∫ÂüüÊåáÁ§∫Á¨¶ËåÉÂõ¥ÂÜÖ
  if (
    codePoint1 >= REGIONAL_INDICATOR_START && codePoint1 <= 0x1F1FF &&
    codePoint2 >= REGIONAL_INDICATOR_START && codePoint2 <= 0x1F1FF
  ) {
    // ÁÆóÊ≥ïËΩ¨Êç¢ÔºöÈÄöËøáËÆ°ÁÆó‰∏é 'A' ÂØπÂ∫îÁöÑÂå∫ÂüüÊåáÁ§∫Á¨¶ÁöÑÂÅèÁßªÈáèÔºåÂ∞ÜÂå∫ÂüüÊåáÁ§∫Á¨¶Á†ÅÁÇπËΩ¨Êç¢ÂõûÂØπÂ∫îÁöÑ ASCII Â≠óÊØçÁ†ÅÁÇπ
    const letter1 = String.fromCodePoint(codePoint1 - REGIONAL_INDICATOR_START + ASCII_ALPHA_START);
    const letter2 = String.fromCodePoint(codePoint2 - REGIONAL_INDICATOR_START + ASCII_ALPHA_START);
    return `${letter1}${letter2}`;
  }

  return null;
};

const Flag = React.memo(({ flag, size }: FlagProps) => {
  let imgSrc: string;
  let altText: string;
  let resolvedFlagFileName: string; // ÊúÄÁªàÁî®‰∫éÊûÑÂª∫Êñá‰ª∂ÂêçÁöÑÂ≠óÁ¨¶‰∏≤ (‰æãÂ¶Ç "SG", "UN")

  // 1. **ÁÆóÊ≥ïÂ§ÑÁêÜÔºö** Â∞ùËØïÂ∞ÜËæìÂÖ•‰Ωú‰∏∫Áî±Âå∫ÂüüÊåáÁ§∫Á¨¶ÁªÑÊàêÁöÑÊóóÂ∏ú emoji ËøõË°åËΩ¨Êç¢
  const countryCodeFromEmoji = getCountryCodeFromFlagEmoji(flag);

  if (countryCodeFromEmoji) {
    resolvedFlagFileName = countryCodeFromEmoji; // ‰æãÂ¶ÇÔºåÂ¶ÇÊûúËæìÂÖ•ÊòØ "üá∏üá¨"ÔºåÂàôËøôÈáåÂæóÂà∞ "SG"
  }
  // 2. **Áõ¥Êé•ËØÜÂà´Ôºö** Â¶ÇÊûú‰∏çÊòØÂå∫ÂüüÊåáÁ§∫Á¨¶ emojiÔºåÊ£ÄÊü•ÊòØÂê¶ÊòØ‰∏§Â≠óÊØçÁöÑÂ≠óÊØçÁªÑÂêàÔºàISO ÂõΩÂÆ∂‰ª£Á†ÅÔºâ
  else if (flag && flag.length === 2 && /^[a-zA-Z]{2}$/.test(flag)) {
    resolvedFlagFileName = flag.toUpperCase(); // ‰æãÂ¶ÇÔºåÂ¶ÇÊûúËæìÂÖ•ÊòØ "us"ÔºåÂàôËøôÈáåÂæóÂà∞ "US"
  }
  // 3. **Á°¨ÁºñÁ†ÅÂ§ÑÁêÜÁâπÊÆä EmojiÔºö** ÂØπ‰∫éÊó†Ê≥ïÈÄöËøáÁÆóÊ≥ïËΩ¨Êç¢ÁöÑÁâπÊÆä emojiÔºà‰æãÂ¶Ç üá∫üá≥, üåêÔºâÔºå
  //    Âõ†‰∏∫ÂÆÉ‰ª¨‰∏çÁ¨¶ÂêàÂå∫ÂüüÊåáÁ§∫Á¨¶Ê®°ÂºèÔºå‰∏î‰∏ç‰ΩøÁî®Êò†Â∞ÑË°®ÔºåÂè™ËÉΩÈÄöËøáÁ°¨ÁºñÁ†ÅÊù•ËØÜÂà´„ÄÇ
  else if (flag === "üá∫üá≥" || flag === "üåê") {
    resolvedFlagFileName = "UN"; // ‰æãÂ¶ÇÔºåÂ¶ÇÊûúËæìÂÖ•ÊòØ "üá∫üá≥"ÔºåÂàôËøôÈáåÂæóÂà∞ "UN"
  }
  // 4. **ÂõûÈÄÄÔºö** ÂØπ‰∫é‰ªª‰ΩïÂÖ∂‰ªñÊó†Ê≥ïËØÜÂà´ÁöÑËæìÂÖ•ÔºàÂåÖÊã¨‰∏çÁ¨¶Âêà‰∏äËø∞ËßÑÂàôÁöÑ emoji ÊàñÈùû‰∏§Â≠óÊØç‰ª£Á†ÅÔºâÔºå
  //    ‰ΩøÁî®ÈªòËÆ§ÁöÑ "UN" ÊóóÂ∏ú‰Ωú‰∏∫ÂõûÈÄÄ„ÄÇ
  else {
    resolvedFlagFileName = "UN";
  }

  // ÊûÑÂª∫Êú¨Âú∞ÂõæÁâáË∑ØÂæÑ
  imgSrc = `/assets/flags/${resolvedFlagFileName}.svg`;
  // ÊûÑÂª∫ alt ÊñáÊú¨Âíå aria-label
  altText = `Âú∞Âå∫ÊóóÂ∏ú: ${resolvedFlagFileName}`;

  return (
    <Box
      as="span"
      className={`m-2 self-center ${size ? `w-${size} h-${size}` : "w-6 h-6"}`}
      style={{ display: "inline-flex", alignItems: "center" }}
      aria-label={altText}
    >
      <img
        src={imgSrc}
        alt={altText}
        style={{ width: "100%", height: "100%", objectFit: "contain" }}
      />
    </Box>
  );
});

// Á°Æ‰øù displayName ‰ª•‰æøÂú® React DevTools ‰∏≠ËØÜÂà´
Flag.displayName = "Flag";

export default Flag;