import React, { useState, useMemo, useRef, useEffect, Suspense } from "react";
import { Search, Grid3X3, Table2, X } from "lucide-react";
import { useTranslation } from "react-i18next";
import { useLocalStorage } from "@/hooks/useLocalStorage";
import type { NodeBasicInfo } from "@/contexts/NodeListContext";
import type { LiveData } from "../types/LiveData";
import { NodeGrid } from "./Node";
const NodeTable = React.lazy(() => import("./NodeTable"));
import { isRegionMatch } from "@/utils/regionHelper";
import "./NodeDisplay.css";

import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs";

export type ViewMode = "grid" | "table";

interface NodeDisplayProps {
  nodes: NodeBasicInfo[];
  liveData: LiveData;
}

const NodeDisplay: React.FC<NodeDisplayProps> = ({ nodes, liveData }) => {
  const [t] = useTranslation();
  const [viewMode, setViewMode] = useLocalStorage<ViewMode>(
    "nodeViewMode",
    "grid"
  );
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedGroup, setSelectedGroup] = useLocalStorage<string>(
    "nodeSelectedGroup",
    "all"
  );
  const searchRef = useRef<HTMLInputElement>(null);

  // Ëé∑ÂèñÊâÄÊúâÁöÑÂàÜÁªÑ
  const groups = useMemo(() => {
    const groupSet = new Set<string>();
    nodes.forEach((node) => {
      if (node.group && node.group.trim()) {
        groupSet.add(node.group);
      }
    });
    return Array.from(groupSet).sort();
  }, [nodes]);

  // Âà§Êñ≠ÊòØÂê¶ÊòæÁ§∫ÂàÜÁªÑÈÄâÊã©Âô®
  const showGroupSelector = groups.length >= 1;

  // ÈîÆÁõòÂø´Êç∑ÈîÆÊîØÊåÅ
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      // Êåâ "/" ÈîÆËÅöÁÑ¶ÊêúÁ¥¢Ê°Ü
      if (e.key === "/" && !e.ctrlKey && !e.metaKey && !e.altKey) {
        e.preventDefault();
        searchRef.current?.focus();
      }
      // Êåâ Escape ÈîÆÊ∏ÖÁ©∫ÊêúÁ¥¢
      if (e.key === "Escape" && searchTerm) {
        setSearchTerm("");
        searchRef.current?.blur();
      }
    };

    document.addEventListener("keydown", handleKeyDown);
    return () => document.removeEventListener("keydown", handleKeyDown);
  }, [searchTerm]);

  // ËøáÊª§ËäÇÁÇπ
  const filteredNodes = useMemo(() => {
    let result = nodes;

    // ÂÖàÊåâÂàÜÁªÑËøáÊª§
    if (selectedGroup !== "all") {
      result = result.filter((node) => node.group === selectedGroup);
    }

    // ÂÜçÊåâÊêúÁ¥¢Êù°‰ª∂ËøáÊª§
    if (!searchTerm.trim()) return result;

    const term = searchTerm.toLowerCase().trim();
    return result.filter((node) => {
      // Âü∫Êú¨‰ø°ÊÅØÊêúÁ¥¢
      const basicMatch =
        node.name.toLowerCase().includes(term) ||
        node.os.toLowerCase().includes(term) ||
        node.arch.toLowerCase().includes(term);

      // Âú∞Âå∫ÊêúÁ¥¢ÔºàÊîØÊåÅemojiÂíåÂú∞Âå∫ÂêçÁß∞Ôºâ
      const regionMatch = isRegionMatch(node.region, term);

      // ‰ª∑Ê†ºÊêúÁ¥¢ÔºàÂ¶ÇÊûúËæìÂÖ•Êï∞Â≠óÔºâ
      const priceMatch =
        !isNaN(Number(term)) && node.price.toString().includes(term);

      // Áä∂ÊÄÅÊêúÁ¥¢
      const isOnline = liveData?.online?.includes(node.uuid) || false;
      const statusMatch =
        ((term === "online" || term === "Âú®Á∫ø") && isOnline) ||
        ((term === "offline" || term === "Á¶ªÁ∫ø") && !isOnline);

      return basicMatch || regionMatch || priceMatch || statusMatch;
    });
  }, [nodes, searchTerm, liveData, selectedGroup]);

  return (
    <div className="w-full space-y-4">
      {/* Control Bar */}
      <div className="flex flex-col sm:flex-row justify-between items-stretch sm:items-center gap-4 bg-card p-4 rounded-lg border shadow-sm">
        {/* Search Box */}
        <div className="relative flex-1 max-w-md flex items-center gap-2">
          <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
          <Input
            ref={searchRef}
            placeholder={t("search.placeholder", {
              defaultValue: "Search nodes...",
            })}
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="pl-9 pr-9"
          />
          {searchTerm && (
            <Button
              variant="ghost"
              size="icon"
              className="absolute right-1 top-1 h-7 w-7"
              onClick={() => {
                setSearchTerm("");
                searchRef.current?.focus();
              }}
            >
              <X className="h-4 w-4" />
            </Button>
          )}
        </div>

        {/* View Mode */}
        <div className="flex items-center gap-2">
          <span className="text-sm text-muted-foreground whitespace-nowrap hidden sm:inline-block">
            {t("view.mode", { defaultValue: "View" })}
          </span>
          <div className="flex items-center gap-1 border rounded-md p-1 bg-muted/50">
            <Button
              variant={viewMode === "grid" ? "secondary" : "ghost"}
              size="icon"
              className="h-8 w-8"
              onClick={() => setViewMode("grid")}
            >
              <Grid3X3 className="h-4 w-4" />
            </Button>
            <Button
              variant={viewMode === "table" ? "secondary" : "ghost"}
              size="icon"
              className="h-8 w-8"
              onClick={() => setViewMode("table")}
            >
              <Table2 className="h-4 w-4" />
            </Button>
          </div>
        </div>
      </div>

      {/* Group Selector */}
      {showGroupSelector && (
        <div className="flex items-center gap-2 overflow-x-auto pb-2 scrollbar-none">
          <span className="text-sm text-muted-foreground whitespace-nowrap pl-1">
            {t("common.group", { defaultValue: "Group" })}
          </span>
          <Tabs value={selectedGroup} onValueChange={setSelectedGroup}>
            <TabsList className="h-9 w-full justify-start">
              <TabsTrigger value="all">
                {t("common.all", { defaultValue: "All" })}
              </TabsTrigger>
              {groups.map((group) => (
                <TabsTrigger key={group} value={group}>
                  {group}
                </TabsTrigger>
              ))}
            </TabsList>
          </Tabs>
        </div>
      )}

      {/* Results Stats */}
      <div className="flex justify-between items-center px-1">
        {searchTerm.trim() ? (
          <span className="text-sm text-muted-foreground">
            {t("search.results", {
              count: filteredNodes.length,
              total:
                selectedGroup === "all"
                  ? nodes.length
                  : nodes.filter((n) => n.group === selectedGroup).length,
              defaultValue: `Found ${filteredNodes.length} nodes, total ${
                selectedGroup === "all"
                  ? nodes.length
                  : nodes.filter((n) => n.group === selectedGroup).length
              }`,
            })}
          </span>
        ) : (
          <span className="text-sm text-muted-foreground">
            {selectedGroup === "all"
              ? t("nodeCard.totalNodes", {
                  total: nodes.length,
                  online: liveData?.online?.length || 0,
                  defaultValue: `Total ${nodes.length} nodes, ${
                    liveData?.online?.length || 0
                  } online`,
                })
              : t("nodeCard.groupNodes", {
                  group: selectedGroup,
                  total: filteredNodes.length,
                  online: filteredNodes.filter((n) =>
                    liveData?.online?.includes(n.uuid)
                  ).length,
                  defaultValue: `${selectedGroup}: Total ${
                    filteredNodes.length
                  } nodes, ${
                    filteredNodes.filter((n) =>
                      liveData?.online?.includes(n.uuid)
                    ).length
                  } online`,
                })}
          </span>
        )}
      </div>

      {/* Node Display Area */}
      {filteredNodes.length === 0 ? (
        <div className="flex flex-col items-center justify-center py-16 bg-muted/20 rounded-lg border border-dashed">
          <span className="text-lg text-muted-foreground mb-2">
            {searchTerm.trim()
              ? t("search.no_results", { defaultValue: "No matching nodes found" })
              : t("nodes.empty", { defaultValue: "No node data" })}
          </span>
          {searchTerm.trim() && (
            <span className="text-sm text-muted-foreground">
              {t("search.try_different", {
                defaultValue: "Try different keywords",
              })}
            </span>
          )}
        </div>
      ) : (
        <>
          {viewMode === "grid" ? (
            <NodeGrid nodes={filteredNodes} liveData={liveData} />
          ) : (
            <Suspense
              fallback={<div className="p-4 text-center">Loading table...</div>}
            >
              <NodeTable nodes={filteredNodes} liveData={liveData} />
            </Suspense>
          )}
        </>
      )}
    </div>
  );
};

export default NodeDisplay;
